import React, { useEffect, useState } from "react";
import { Card, CardBody } from "react-bootstrap";
import { getFlagCode, hideLangFilter } from "../../../../../utils";
import AssetsbySkeleton from "../../layout/skeleton/assetsbySkeleton";
import NoData from "../../layout/common/noData";
import FilterModeration from "../../layout/common/filterModeration";
import SectionHeading from "../../layout/common/sectionHeading";

const AssetsLanguage = (props) => {

  const { getFlags, loader, selectedWallFeed, postbyLanguage, dateFilter } = props;
  
  const [getLanguage, setgetLanguage]= useState()

    const languageTags=()=>{

      const feedList = selectedWallFeed && Object.keys(selectedWallFeed).length > 0 ? selectedWallFeed.map(item => item.id) : [];
          let langs = postbyLanguage;
          let result = [];

          if(langs && langs.length > 0){
              langs = langs.filter((lang) => {
                  if(feedList.length){
                     if(hideLangFilter(feedList, lang.feedId,1)) return lang;
                       }else{
                    return lang;
                  }
              });
              langs.splice(6).sort((a, b) => b.count - a.count).forEach(lang => {
                  if(lang.language != ""){
                      result.push(lang.language);
                  }
              });
 
              setgetLanguage(langs)
          }
      }
 

  useEffect(() => {
    selectedWallFeed && languageTags()
  }, []);

  
  return (
    <Card className="sw-card-custom gutter-b card-shadow mb-5">
      <SectionHeading  section={2}  title={'Top Languages'} />
      <CardBody className="p-5">  
        <div className="mx-height-345">
        { loader ? <AssetsbySkeleton/> : getLanguage && Object.keys(getLanguage).length
          ? getLanguage.map((data,index) => {
              return (
              <FilterModeration dateFilter={dateFilter} selectedWallFeed={selectedWallFeed} link={`language=${data.language}`}>
                <div className="px-5 py-2 border rounded">
                  <div className="d-flex align-items-center justify-content-between">
                    <div className="d-flex align-items-center me-10">
                      <div className="rounded me-3 d-flex">
                        <div class={`iti-flag ${getFlagCode(data.language,getFlags)}`}></div> 
                      </div>
                      <div className="">
                        <p className="text-dark mb-0 font-weight-boldest overflow-hidden text-nowrap text-ellipsis author_name text-capitalize">
                           { data.language }
                        </p>
                      </div>                 
                    </div>
                    <p className="text-dark count-tag border font-weight-boldest m-0">
                     { data.count }
                    </p>
                  </div>
                  </div>
                  </FilterModeration>  
                );
              })
            : <NoData/>}
        </div>
         
        </CardBody>
      </Card>
  );
};

export default React.memo(AssetsLanguage);
